"use client";
import { useState, useEffect, useRef } from "react";

export default function Home() {
  const [isJumping, setIsJumping] = useState(false);
  const [position, setPosition] = useState(0);
  const [obstacleLeft, setObstacleLeft] = useState(500);
  const [isGameOver, setIsGameOver] = useState(false);
  const gameRef = useRef<HTMLDivElement>(null);

  // ジャンプ処理
  const jump = () => {
    if (isJumping) return;
    setIsJumping(true);
    let upInterval = setInterval(() => {
      setPosition(prev => {
        if (prev >= 120) {
          clearInterval(upInterval);
          let downInterval = setInterval(() => {
            setPosition(p => {
              if (p <= 0) {
                clearInterval(downInterval);
                setIsJumping(false);
                return 0;
              }
              return p - 20;
            });
            return p;
          }, 20);
        }
        return prev + 20;
      });
    }, 20);
  };

  // サボテン移動
  useEffect(() => {
    if (isGameOver) return;
    const interval = setInterval(() => {
      setObstacleLeft(prev => (prev > -20 ? prev - 5 : 500));
    }, 20);
    return () => clearInterval(interval);
  }, [isGameOver]);

  // 衝突判定
  useEffect(() => {
    if (obstacleLeft < 50 && obstacleLeft > 0 && position < 50) {
      setIsGameOver(true);
    }
  }, [obstacleLeft, position]);

  // スペースキーでジャンプ
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.code === "Space") jump();
    };
    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, [isJumping]);

  return (
    <div
      ref={gameRef}
      className="relative w-[500px] h-[200px] border border-gray-400 overflow-hidden bg-gray-100 mt-10 mx-auto"
    >
      {isGameOver && (
        <div className="absolute inset-0 flex items-center justify-center bg-white/70 text-2xl font-bold text-red-600">
          Game Over
        </div>
      )}
      <div
        className="absolute bottom-0 left-[50px] w-[40px] h-[40px] bg-green-600 rounded"
        style={{ bottom: `${position}px` }}
      ></div>
      <div
        className="absolute bottom-0 w-[20px] h-[40px] bg-gray-700"
        style={{ left: `${obstacleLeft}px` }}
      ></div>
    </div>
  );
}
